/* global document, Office, Word */
window.identifiedTerms = [];

Office.onReady((info) => {
  const stored = localStorage.getItem("identifiedTerms");
  try {
    const parsed = JSON.parse(stored);
    if (Array.isArray(parsed) && parsed.length > 0 && typeof parsed[0] === "object") {
      console.log("üßπ Ê∏ÖÈô§ÊóßÊ†ºÂºè identifiedTerms...");
      localStorage.removeItem("identifiedTerms");
    }
  } catch (e) {
    console.warn("‚ö†Ô∏è Êó†Ê≥ïËß£Êûê localStorage ‰∏≠ÁöÑ identifiedTerms");
    localStorage.removeItem("identifiedTerms");
  }

  if (info.host === Office.HostType.Word) {
    console.log("‚úÖ Office is ready");

    const sideloadMsg = document.getElementById("sideload-msg");
    if (sideloadMsg) sideloadMsg.style.display = "none";

    const appBody = document.getElementById("app-body");
    if (appBody) appBody.style.display = "flex";

    const runBtn = document.getElementById("run");
    if (runBtn) runBtn.addEventListener("click", run);

    const quoteBtn = document.getElementById("terms-quote");
    if (quoteBtn) quoteBtn.addEventListener("click", termsquote);

    const sortBtn = document.getElementById("sort-terms");
    if (sortBtn) sortBtn.addEventListener("click", sortDefinitionParagraphs);

    const highlightBtn = document.getElementById("terms-hightlight");
    if (highlightBtn) highlightBtn.addEventListener("click", highlightIdentifiedTerms);

    const openBtn = document.getElementById("open-term-manager");
    if (openBtn) {
      let dialogRef = null;
      openBtn.addEventListener("click", () => {
        console.log("üü¢ Launching term-manager dialog...");
        Office.context.ui.displayDialogAsync(
          window.location.origin + "/term-manager.html",
          { height: 50, width: 50, displayInIframe: true },
          (asyncResult) => {
            if (asyncResult.status === Office.AsyncResultStatus.Succeeded) {
              dialogRef = asyncResult.value;
              dialogRef.addEventHandler(Office.EventType.DialogMessageReceived, (arg) => {
                if (arg.message === "close") {
                  dialogRef.close();
                  console.log("üîí Dialog closed by child page");
                }
              });
            } else {
              console.error("‚ùå Dialog failed to open:", asyncResult.error.message);
            }
          }
        );
      });
    }
  }
});

export async function run() {
  return Word.run(async (context) => {
    const paragraph = context.document.body.insertParagraph("Hello World", Word.InsertLocation.end);
    paragraph.font.color = "blue";
    await context.sync();
  });
}

async function termsquote() {
  await Word.run(async (context) => {
    const autoAddQuotes = document.getElementById("auto-add-quotes")?.checked ?? true;
    const autoUnderline = document.getElementById("auto-underline")?.checked ?? true;
    const autoBold = document.getElementById("auto-bold")?.checked ?? true;

    const paragraphs = context.document.getSelection().paragraphs;
    paragraphs.load("items");
    await context.sync();

    paragraphs.items.forEach((para) => para.load("text"));
    await context.sync();

    for (const para of paragraphs.items) {
      const originalText = para.text;
      const match = originalText.match(/(.+?)\s+(means|mean|includes|has the meaning)\b/i);
      if (!match) continue;

      const rawTerms = match[1].trim();
      const keyword = match[2];
      const definition = originalText.slice(match.index + match[0].length).trim();

      const { terms, joiners } = tokenizeTerms(rawTerms);
      const formattedTerms = [];

      for (const term of terms) {
        const bracketMatch = term.match(/^(.+?)(\s*\(.*\))?$/);
        const cleanTerm = bracketMatch[1].replace(/^['"‚Äú‚Äù]|['"‚Äú‚Äù]$/g, '').trim();

        if (!window.identifiedTerms.some((item) => item.term === cleanTerm)) {
          window.identifiedTerms.push({ term: cleanTerm, keyword, note: definition });
        }

        let formatted = autoAddQuotes ? `‚Äú${cleanTerm}‚Äù` : cleanTerm;
        if (bracketMatch[2]) formatted += bracketMatch[2];

        formattedTerms.push(formatted);
      }

      const rebuiltTerms = formattedTerms.map((t, i) => t + (joiners[i] ? ` ${joiners[i]} ` : '')).join('').trim();
      const newText = `${rebuiltTerms} ${keyword} ${definition}`;

      const inserted = para.insertText(newText, Word.InsertLocation.replace);
      inserted.load("text");
      await context.sync();

      // Âè™Âú®ÂÖ≥ÈîÆËØçÂâçÊêúÁ¥¢
      const keywordPosition = inserted.text.indexOf(` ${keyword} `);
      const termsSearchArea = inserted.getRange("Start").expandTo(inserted.getRange("Start").getTextRanges([` ${keyword} `], false).getFirst());

      for (const term of formattedTerms) {
        const coreTerm = term.replace(/^['"‚Äú‚Äù]|['"‚Äú‚Äù]$/g, '').replace(/\(.*\)$/, '').trim();
        const results = termsSearchArea.search(coreTerm, { matchCase: false, matchWholeWord: true });
        results.load("items");
        await context.sync();

        if (results.items.length === 0) continue;

        const r = results.items[0]; // Âè™Â§ÑÁêÜÁ¨¨‰∏Ä‰∏™ÂåπÈÖç
        r.load("text, font");
        await context.sync();

        // Â§ÑÁêÜÂèØËÉΩÂ≠òÂú®ÁöÑÂºïÂè∑
        const startsWithQuote = /^['"‚Äú‚Äù]/.test(r.text);
        const endsWithQuote = /['"‚Äú‚Äù]$/.test(r.text);

        if (startsWithQuote || endsWithQuote) {
          const subRanges = r.split(["\"", "‚Äú", "‚Äù"], true);
          subRanges.load("items");
          await context.sync();

          for (const sub of subRanges.items) {
            if (!/^['"‚Äú‚Äù]+$/.test(sub.text.trim()) && sub.text.trim()) {
              if (autoBold) sub.font.bold = true;
              if (autoUnderline) sub.font.underline = "Single";
            }
          }
        } else {
          if (autoBold) r.font.bold = true;
          if (autoUnderline) r.font.underline = "Single";
        }
      }
    }

    await context.sync();
    localStorage.setItem("identifiedTerms", JSON.stringify(window.identifiedTerms));
    showToast("Term identification and formatting completed.");
  });
}

function tokenizeTerms(raw) {
  const terms = [];
  const joiners = [];
  const splitRegex = /(.+?)(\s*,\s*|\s+or\s+|\s+and\s+|$)/gi;

  let match;
  while ((match = splitRegex.exec(raw)) !== null) {
    const term = match[1].trim();
    const joiner = match[2]?.trim();
    if (term) terms.push(term);
    if (joiner) joiners.push(joiner);
  }

  return { terms, joiners };
}



function processDefinitionText(text, options = {}) {
  const triggerWords = ["means", "mean", "has the meaning", "includes"];
  const regex = new RegExp(`(.+?)\\s+(${triggerWords.join("|")})\\b`, "i");
  const match = text.match(regex);
  if (!match) return { replacedText: text, styleTerms: [] };

  const rawTerms = match[1].trim();
  const keyword = match[2];
  const rest = text.slice(match.index + match[0].length).trim();

  const { terms, joiners } = tokenizeTerms(rawTerms);
  const styleTerms = [];

  const formattedTerms = terms.map((term) => {
    const m = term.match(/^(.+?)(\s*\(.*\))?$/);
    if (!m) return term;

    let core = m[1].trim();
    const extra = m[2] || "";

    core = core.replace(/^["‚Äú]/, "").replace(/["‚Äù]$/, "");

    // ‚úÖ ‰øùÂ≠òÂéªÂºïÂè∑ÁâàÊú¨
    if (!window.identifiedTerms.some((item) => item.term === core)) {
      window.identifiedTerms.push({ term: core, keyword, note: rest });
    }

    // ‚úÖ ÊääÂéªÂºïÂè∑ÁâàÊú¨Âä†ÂÖ• styleTerms Áî®‰∫éÊêúÁ¥¢ÂåπÈÖç
    styleTerms.push(core);

    // ‚úÖ ÊääÊúâÂºïÂè∑ÁöÑÁâàÊú¨‰πüÂä†ÂÖ• styleTermsÔºàÊñπ‰æøÁ≤æÂáÜÂåπÈÖçÂêéÂ§ÑÁêÜÔºâ
    if (options.autoAddQuotes) {
      styleTerms.push(`‚Äú${core}‚Äù`);
      styleTerms.push(`"${core}"`);
    }

    const display = options.autoAddQuotes ? `‚Äú${core}‚Äù` : core;
    return `${display}${extra}`;
  });

  let rebuilt = "";
  for (let i = 0; i < formattedTerms.length; i++) {
    rebuilt += formattedTerms[i];
    if (i < joiners.length) rebuilt += ` ${joiners[i]} `;
  }

  const replacedText = `${rebuilt.trim()} ${keyword} ${rest}`;
  return { replacedText, styleTerms };
}


function isConnector(token) {
  return /^(,|and|or)$/i.test(token.trim());
}

async function sortDefinitionParagraphs() {
  await Word.run(async (context) => {
    const selection = context.document.getSelection();
    const paragraphs = selection.paragraphs;
    paragraphs.load("items");
    await context.sync();
    
    selection.load("text");
    await context.sync();
    // ‚úÖ Ê£ÄÊü•ÊòØÂê¶ÊúâÈÄâ‰∏≠ÂÜÖÂÆπ
    if (!selection.text || selection.text.trim() === "") {
      showToast("Please select content before sorting terms.");
      return;
    }

    const paragraphData = [];

    for (const para of paragraphs.items) {
      const range = para.getRange();
      range.load(["text", "font/bold", "font/color", "font/italic", "font/underline"]);
      await context.sync();

      const text = range.text?.trim();
      if (!text) continue;

      const match = text.match(/‚Äú(.+?)‚Äù/);
      const key = match ? match[1].toLowerCase() : text.toLowerCase();

      paragraphData.push({
        key,
        text,
        font: {
          color: range.font.color || "auto",
          bold: typeof range.font.bold === "boolean" ? range.font.bold : false,
          italic: typeof range.font.italic === "boolean" ? range.font.italic : false,
          underline: range.font.underline || "None",
        },
      });
    }

    // Êåâ key ÂçáÂ∫èÊéíÂàó
    paragraphData.sort((a, b) => a.key.localeCompare(b.key, undefined, { sensitivity: "base" }));

    // ÊèíÂÖ•Êñ∞ÊÆµËêΩ
    let insertAfter = paragraphs.items[paragraphs.items.length - 1];
    for (const item of paragraphData) {
      const newPara = insertAfter.insertParagraph("", Word.InsertLocation.after);
      const insertedRange = newPara.insertText(item.text, Word.InsertLocation.replace);

      insertedRange.font.color = item.font.color;
      insertedRange.font.bold = item.font.bold;
      insertedRange.font.italic = item.font.italic;
      insertedRange.font.underline = item.font.underline;

      insertAfter = newPara;
    }

    await context.sync();

    // Âà†Èô§ÂéüÈÄâ‰∏≠ÊÆµËêΩ
    for (let i = paragraphs.items.length - 1; i >= 0; i--) {
      paragraphs.items[i].delete();
    }

    await context.sync();
    showToast("Sorting completed.\nTo undo this operation, please press Ctrl + Z twice.");
  });
}


function showToast(message, duration = 5000) {
  const toast = document.getElementById("custom-toast");
  if (!toast) return;
  toast.innerHTML = message.replace(/\n/g, "<br>");
  toast.style.display = "block";
  setTimeout(() => {
    toast.style.display = "none";
  }, duration);
}

async function highlightIdentifiedTerms() {
  await Word.run(async (context) => {
    if (!window.identifiedTerms || window.identifiedTerms.length === 0) {
      showToast("No identified terms found. Please identify terms first.");
      return;
    }

    const highlightSelect = document.getElementById("highlight-style");
    const style = highlightSelect ? highlightSelect.value : "underline";

    const selection = context.document.getSelection();
    selection.load("text");
    await context.sync();

    if (!selection.text || selection.text.trim() === "") {
      showToast("Please select the content area where you want to highlight terms.");
      return;
    }

    for (const item of window.identifiedTerms) {
      const term = typeof item === "string" ? item : item.term;
      if (!term || typeof term !== "string") continue;

      const searchResults = selection.search(term, { matchWholeWord: true });
      searchResults.load("items");
      await context.sync();

      for (const range of searchResults.items) {
        if (style === "underline") {
          range.font.underline = "Single";
        } else if (style === "bold") {
          range.font.bold = true;
        }
      }
    }

    await context.sync();
    showToast("Term highlighting completed.");
  });
}
